:root{
    --fuenteHeading: 'PT Sans', sans-serif;
    --funeteParrafos: 'Open Sans', sans-serif;

    --colorPrimario: #FF5733;
    --colorNegro: #000000;
    --colorGris: #757575;
    --colorBlanco: #ffffff;
    --grisclaro: #DFE9F3;
}


html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

body {
    font-family: var(--funeteParrafos);
    font-size: 1.6rem;
    line-height: 2;
    background-image: linear-gradient(to top, var(--grisclaro) 0%, var(--colorBlanco) 100%);
}

a {
    text-decoration: none;
}

/* a:hover {
    color: var(--colorPrimario);
}
*/

h1, h2, h3 {
    font-family: var(--fuenteHeading);
    line-height: 1.2;
}

h1 {
    font-size: 4.8rem;
}

img {
    max-width: 100%;
    border: none;
}

.contenedor {
    max-width: 120rem;
    width: 90%;
    margin: 0 auto;
}


/* Navegacion */
/* .webp .img-inicio{
    background-image: url(../../build/img/2.webp);
} */
.img-inicio{
    background-image: url(../../build/img/2.webp);  

}
.img-inicio {/* 
    position: relative; */
    height:  60rem;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    /* text-align: center; */
}

.header {
    /* position: fixed; */
    width: 100%;
    /* z-index: 10; */
    position: absolute;/* 
    transition: 0.3s; */
}

.contenido-header {
    /* padding-top: 4rem; */
    padding-top: 4rem;

}

@media (min-width: 768px) {
    .contenido-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align: center;
        
    }

}

.navegacion__enlace {
    display: block;
    font-size: 1.8rem; 
    margin-bottom: 1rem;
    color: var(--colorBlanco);
    text-align: center;
    }

.navegacion__enlace:hover {
        color: var(--colorPrimario);
}

@media(min-width:768px){
    .navegacion{
        display: flex;
        gap: 2rem;
    }
}


.text_bold{
    font-weight: 700;     
}

/*.cv {
    text-align: center;
    /* margin-top: 2rem; 
} */


.cv {
    text-align: center;
    padding-top: 2rem;
}

@media(min-width: 768px) {
    .cv {
        text-align: center;
        padding: 0;
    }
}

.btn-cv {
    padding: 1rem;
    background-color: rgba( 0 0 0 / 60% );
    color: var(--colorBlanco);
    font-size: 1.5rem;
    text-transform: uppercase;
    border-radius: .5rem;
}

.header__texto {
    position: absolute;
    text-align: center;
    width: 100%;
    color: var(--colorBlanco);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 28rem;
    /* position: absolute; */
    /* width: 100%; *//* 
    height: 100%; */
    /* margin-top: 28rem; */
  /*   display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; */

    /*margin-left: 2rem; */
}
/* .nombre {
    
    backdrop-filter: blur(20px);
} */

.header__texto p {
    margin-top: -.1rem;
}


@media(min-width: 768px){
    .header__texto {
        margin-top: 22rem;
    
    /* margin-top: 28rem;  */
    /* display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; */

    }
}

@media(min-width:768px){
    .contenido-principal {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 2rem;
    }
}





.footer {
    padding: 1rem 0;
    background-color: var(--colorNegro);
    margin-top: 4rem;
    color: white;
}


.navegacion-footer {
    max-width: 10rem;
    display: flex;
    column-gap: 1rem; 
    margin: 0 auto;
}


.texto-footer {
    text-align: center;
    font-size: 2rem;
    margin: .5rem;
}


.sombra {
    -webkit-box-shadow: 0px 5px 15px 0px rgba(112,112,112,0.48);
    -moz-box-shadow: 0px 5px 15px 0px rgba(112,112,112,0.48);
    box-shadow: 0px 5px 15px 0px rgba(112,112,112,0.48);
    background-color: var(--colorBlanco);
    padding: 2rem;
    border-radius: 1rem;  
    margin: -15rem auto 0 auto;
}


/* @media( min-width: 768px) {
    .lista-habilidades {
        display: flex;
        flex-direction: row;
        gap: 1.5rem;
    }
} */
@media(min-width: 350px ) {
 .lista-habilidades {
    display: flex;
    flex-direction: row;
    gap: 1.5rem;
}
}



.none {
    list-style: none;
    padding: 0;
}

@media(min-width: 768px) {
    .lista {
        display: grid;
        gap: 2rem;
        grid-template-columns: repeat(2, 1fr);
    } 
}

@media(min-width: 1100px) {
    .lista {
        display: grid;
        gap: 2rem;
        grid-template-columns: repeat(3, 1fr);
       
    }   
}
