.btn-card {
    /* background-color: rgba( 0 0 0 / 60% ); */
    background-color: #33C3F0;
    color: var(--colorBlanco);
    font-weight: 700;
    text-decoration: none;
    padding: .5rem 4rem;
    margin-top: 2.5rem ;
