@media(min-width: 768px) {
    .prueba {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        gap: 3rem; 

    }
}



@media(min-width: 768px) {
    .fijo {
        position: fixed; 
        background-color: rgba( 0 0 0 / 60% );
        left: 0;
        top: 0;  
        width: 100%;
        box-shadow: .1rem .2rem .3rem #333;
        /* justify-content: space-around; */
        z-index: 10;
        transition: 0.7s;
    } 

    .cambio {
        padding: .5rem;
    }
    .cambio:hover {

            background-color: rgba( 255 255 255 / 60% );
            color: var(--colorNegro);
        
    }
}
